 import React, { Component } from 'react';
 import Row from 'react-bootstrap/Row';
 import Col from 'react-bootstrap/Col';
 import {isMobile} from 'react-device-detect';
 import {Helmet} from "react-helmet";
 import Carousel from 'react-multi-carousel';
 import 'react-multi-carousel/lib/styles.css';
 
 import { Footer, Header } from "../components";
 import partner1 from "../images/partner1.jpg";
import partner2 from "../images/partner2.jpg";
import partner3 from "../images/partner3.png";
import partner4 from "../images/partner4.png";
import partner5 from "../images/partner5.png";
import partner6 from "../images/partner6.png";
import partner7 from "../images/partner7.jpg";
import partner8 from "../images/partner8.png";
import partner9 from "../images/partner9.jpg";
import partner10 from "../images/partner10.jpg";
import partner11 from "../images/partner11.jpg";
import partner12 from "../images/partner12.jpg";
import partner13 from "../images/partner13.jpg";
import partner14 from "../images/partner14.jpg";
import partner15 from "../images/partner15.png";
import partner16 from "../images/partner16.jpg";
import partner17 from "../images/partner17.png";
import partner18 from "../images/partner18.jpg";

import apropos from "../images/apropos.jpg";

const carouselResponsive = {
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 5
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 4
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
      slidesToSlide: 1
    }
  };

 class Apropos extends Component {
 
     render(){
 
         if(isMobile) {
             return <p>Mobile detected</p>;
         }else{
             return (
                 <Row className="w-100 m-0">
                     <Helmet>
                         <title>CAMPUS UNIVERSEL - Du rêve à la réalité</title>
                         <meta 
                             name="description" 
                             content="Campus Universel est l’Agence de Promotion des études Supérieures à l’Etranger. Nous orientons les étudiants au Cameroun, 
                             les accompagnons pour les procédures de voyages et les accueillons puis les installons dans plus de 15 pays cibles dans le monde."
                         />
                     </Helmet>
                     <Header />
                     <section className="section-apropos">
                         <Row className="w-100 m-0">
                            <Col md="4" className="m-0 p-0">
                                <img src={apropos} alt="" width="400px" />
                            </Col>
                            <Col md="8">
                                <h1>à propos de nous</h1>
                                <p>Campus Universel est l’Agence de Promotion des études Supérieures à l’Etranger. 
                                    Nous orientons les étudiants au Cameroun, les accompagnons pour les procédures 
                                    de voyages et les accueillons puis les installons dans plus de 15 pays
                                     cibles dans le monde. Constituée d’une équipe Jeune Responsable, notre vision 
                                     est de permettre à tout jeune camerounais d’intégrer une grande école de rêve, 
                                     de renommée internationale avec plus de 45 domaines à disposition. </p>
                                <p>Notre mission est de: <br />
                                    <ul>
                                        <li>Promouvoir au Cameroun des études supérieures étrangères;</li>
                                        <li>Organiser des tests de sélection pour l’accès dans les meilleures écoles d’Ingénierie de certains pays;</li>
                                        <li>Superviser les admissions et inscriptions des étudiants dans les établissements de leur choix;</li>
                                        <li>Fournir une assistance Totale pour l’obtention du Visa;</li>
                                        <li>Rechercher des logements pour étudiants dans le pays de destination;</li>
                                        <li>Accompagner des étudiants à l’insertion dans le pays choisi;</li>
                                        <li>Assister à l’établissement de la Carte de Séjour;</li>
                                        <li>Accueillir à l’aéroport et installer des étudiants dès leur arrivée dans le pays de destination ;</li>
                                        <li>Rechercher des jobs dans les pays où cela est possible.</li>
                                    </ul>
                                </p>
                            </Col>                            
                         </Row>                         
                     </section>
                     <section className="w-100 partner-section mt-0">
                        <Carousel
                            swipeable={false}
                            draggable={false}
                            showDots={false}
                            arrows={false}
                            responsive={carouselResponsive}
                            infinite={true}
                            autoPlay={true}
                            autoPlaySpeed={2000}
                            keyBoardControl={true}
                            customTransition="all .5"
                            transitionDuration={500}
                            containerClass="partner-carousel-container"
                            itemClass="partner-carousel-item" >
                                <img src={partner1} alt="Partenaire" height="90px" />
                                <img src={partner2} alt="Partenaire" height="90px" />
                                <img src={partner3} alt="Partenaire" height="90px" />
                                <img src={partner4} alt="Partenaire" height="90px" />
                                <img src={partner5} alt="Partenaire" height="90px" />
                                <img src={partner6} alt="Partenaire" height="90px" />
                                <img src={partner7} alt="Partenaire" height="90px" />
                                <img src={partner8} alt="Partenaire" height="90px" />
                                <img src={partner9} alt="Partenaire" height="90px" />
                                <img src={partner10} alt="Partenaire" height="90px" />
                                <img src={partner11} alt="Partenaire" height="90px" />
                                <img src={partner12} alt="Partenaire" height="90px" />
                                <img src={partner13} alt="Partenaire" height="90px" />
                                <img src={partner14} alt="Partenaire" height="90px" />
                                <img src={partner15} alt="Partenaire" height="90px" />
                                <img src={partner16} alt="Partenaire" height="90px" />
                                <img src={partner17} alt="Partenaire" height="90px" />
                                <img src={partner18} alt="Partenaire" height="90px" />                            
                        </Carousel>
                    </section>
                    <Footer />                 
                 </Row>
             );
         }
     }
 
 }
 
 export default Apropos;