 import React, { Component } from 'react';
 import { Link } from 'react-router-dom';
 import Row from 'react-bootstrap/Row';
 import Col from 'react-bootstrap/Col';
 import {isMobile} from 'react-device-detect';
 

 import logolongwhite from "../images/logo-long-white.png";

 class Header extends Component {
 
     render(){
 
         if(isMobile) {
             return <p>Mobile detected</p>;
         }else{
             return (
                <section className="w-100 m-0">
                    <Row className="w-100 m-0 hero-section">
                        <Row className="w-100 m-0 nav-section">
                            <Col md="5" className="logo-section">
                                <Link to="/"><img src={logolongwhite} alt="Logo" className="nav-logo" /></Link>
                            </Col>
                            <Col md="7" className="nav-container">
                                <nav>
                                    <Link to="/">Accueil</Link>
                                    <Link to="/">Procédures</Link>
                                    <Link to="/ecolefiliere">écoles et filières</Link>
                                    <Link to="/apropos">à propos</Link>
                                    <span className="nav-decoration"></span>
                                </nav>
                            </Col>
                        </Row> 
                    </Row>
                </section> 
            );
         }
     }
 
 }
 
 export default Header;