import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import {isMobile} from 'react-device-detect';
import {Helmet} from "react-helmet";
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';

import { Footer } from "../components";

import heroimage from "../images/hero.jpg";
import logolongwhite from "../images/logo-long-white.png";
import partner1 from "../images/partner1.jpg";
import partner2 from "../images/partner2.jpg";
import partner3 from "../images/partner3.png";
import partner4 from "../images/partner4.png";
import partner5 from "../images/partner5.png";
import partner6 from "../images/partner6.png";
import partner7 from "../images/partner7.jpg";
import partner8 from "../images/partner8.png";
import partner9 from "../images/partner9.jpg";
import partner10 from "../images/partner10.jpg";
import partner11 from "../images/partner11.jpg";
import partner12 from "../images/partner12.jpg";
import partner13 from "../images/partner13.jpg";
import partner14 from "../images/partner14.jpg";
import partner15 from "../images/partner15.png";
import partner16 from "../images/partner16.jpg";
import partner17 from "../images/partner17.png";
import partner18 from "../images/partner18.jpg";

const carouselResponsive = {
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 5
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 4
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
      slidesToSlide: 1
    }
  };

class Home extends Component {

    render(){

		if(isMobile) {
			return <p>Mobile detected</p>;
        }else{
            return (
                <Row className="w-100 m-0">
					<Helmet>
						<title>CAMPUS UNIVERSEL - Du rêve à la réalité</title>
                        <meta 
                            name="description" 
                            content="Campus Universel est l’Agence de Promotion des études Supérieures à l’Etranger. Nous orientons les étudiants au Cameroun, 
                            les accompagnons pour les procédures de voyages et les accueillons puis les installons dans plus de 15 pays cibles dans le monde."
                        />
					</Helmet>
                    <section className="w-100 m-0">
                        <Row className="w-100 m-0 hero-section">
                            <Row className="w-100 m-0 nav-section">
                                <Col md="5" className="logo-section">
                                    <Link to="/"><img src={logolongwhite} alt="Logo" className="nav-logo" /></Link>
                                </Col>
                                <Col md="7" className="nav-container">
                                    <nav>
                                        <Link to="/">Accueil</Link>
                                        <Link to="/">Procédures</Link>
                                        <Link to="/ecolefiliere">écoles et filières</Link>
                                        <Link to="/apropos">à propos</Link>
                                        <span className="nav-decoration"></span>
                                    </nav>
                                </Col>
                            </Row>
                            <Col md="5" className="hero-section-1 m-0 p-0">
                                <span className="hero-image-border-top"></span>
                                <img src={heroimage} alt="" />
                                <span className="hero-image-border-bottom-block">
                                    <span className="hero-image-border-bottom"></span>
                                </span>                                
                            </Col>
                            <Col md="7" className="hero-section-2">
                                <section>
                                    <h1>Poursuivez vos études</h1>
                                    <h2>Dans le pays de votre choix</h2>
                                    <a href="https://forms.gle/mbJirnQb1Ad1s35z7">
                                        <Button>Demandez plus d'informations</Button>
                                    </a>
                                    <div className="hero-country-contain">
                                        <span className="hero-country-big color-one">Tunisie</span>
                                        <span className="hero-country-big color-two">Maroc</span>
                                        <span className="hero-country-big color-three">France</span>
                                        <span className="hero-country-big color-four">Belgique</span>                                        
                                    </div>
                                    <div className="hero-country-small-contain">
                                        <span className="hero-country-small">Guinée Conakry</span>
                                        <span className="hero-country-small">-</span>
                                        <span className="hero-country-small">Italie</span>
                                        <span className="hero-country-small">-</span>
                                        <span className="hero-country-small">Turquie</span>
                                        <span className="hero-country-small">-</span>
                                        <span className="hero-country-small">Chypre</span>
                                        <span className="hero-country-small">-</span>
                                        <span className="hero-country-small">Mali</span>    
                                    </div>
                                </section>                           
                            </Col>
                            <Row className="w-100 m-0 hero-filiere-contain">
                                <span className="hero-filiere background-color-one">Ingénierie</span>   
                                <span className="hero-filiere background-color-two">Géomatique</span>     
                                <span className="hero-filiere background-color-three">Architecture</span>    
                                <span className="hero-filiere background-color-four">Aéronautique</span>    
                                <span className="hero-filiere background-color-one">Management</span>    
                                <span className="hero-filiere background-color-two">Médecine</span>    
                            </Row>   
                        </Row>
                    </section>
                    <section className="w-100 statistic-section">
                        <Row className="w-100 m-0">
                            <Col md="3" className="statistic-element">
                                <h3>100%</h3>
                                <p>D'obtention d'admission</p>                            
                            </Col>
                            <Col md="3" className="statistic-element">
                                <h3>+20</h3>
                                <p>écoles partenaires</p>                            
                            </Col>
                            <Col md="3" className="statistic-element">
                                <h3>+45</h3>
                                <p>Filières disponibles</p>                            
                            </Col>
                            <Col md="3" className="statistic-element">
                                <h3>+10</h3>
                                <p>Destinations à travers le monde</p>                            
                            </Col>
                        </Row> 
                    </section>  
                    <section className="w-100 service-section">
                        <Row className="w-100 m-0">
                            <h2>L'essentiel pour réaliser vos rêves ...</h2>
                            <Row className="w-100 m-0">
                                <Col md="4" className="service-element">
                                    <span className="service-element-index">01</span>  
                                    <span className="service-element-contain">
                                        <span className="service-element-title">Orientation professionnelle des élèves</span>  
                                        <span className="service-element-description">Notre bureau d'orientation propose aux apprenants un accompagnement
                                            d'orientation scolaire entièrement personnalisé, sous la supervision 
                                            étroite de conseillers expérimentés qui vont les aider dans leur choix 
                                            de formation, d'école ou d'université.
                                        </span>  
                                    </span>                          
                                </Col>
                                <Col md="4" className="service-element">
                                    <span className="service-element-index">02</span>  
                                    <span className="service-element-contain">
                                        <span className="service-element-title">Organisation des Tests de sélection ou 
                                        étude des dossiers des candidats</span>  
                                        <span className="service-element-description">
                                            Une fois le choix de la carrière et de l'école adaptée, CAMPUS UNIVERSEL étudie 
                                            les dossiers des candidats et fait passer des tests de sélection à l'élève niveau BAC 
                                            ou sous réserve, afin de s'assurer que celui-ci possède les bases nécessaires à son choix 
                                            de carrière. 
                                        </span>  
                                    </span>                          
                                </Col>
                                <Col md="4" className="service-element">
                                    <span className="service-element-index">03</span>  
                                    <span className="service-element-contain">
                                        <span className="service-element-title">Lancement des procédures d'inscription des étudiants</span>  
                                        <span className="service-element-description">
                                            A ce stade, l'élève a déjà fait son choix de carrière et d'école; une fois les tests passés, 
                                            nous initions donc les procédures avec les écoles partenaires, qui vont aboutir sur une réservation 
                                            de place et un paiement de la première trance de la scolarité de cet étudiant.
                                        </span>  
                                    </span>                          
                                </Col>
                                <Col md="4" className="service-element">
                                    <span className="service-element-index">04</span>  
                                    <span className="service-element-contain">
                                        <span className="service-element-title">Assistance totale pour l'obtention du passeport et du visa</span>  
                                        <span className="service-element-description">
                                            CAMPUS UNIVERSEL a réussi avec succès 100% des visas étudiants demandés depuis 
                                            son ouverture en 2018. Nous prenons en charge toutes les formalités et nous vous 
                                            accompagnons dans la globalité des étapes jusqu'à obtention de votre visa étudiant. 
                                        </span>  
                                    </span>                          
                                </Col>
                                <Col md="4" className="service-element">
                                    <span className="service-element-index">05</span>  
                                    <span className="service-element-contain">
                                        <span className="service-element-title">Recherche de logements et jobs pour étudiants dans les pays de destination</span>  
                                        <span className="service-element-description">
                                            Nous sommes à votre service pour vous aider à trouver le type de logement ou de job le mieux adapté pour vous. 
                                            Un logement selon votre budget et votre style de vie personnel, ou un stage en fonction de votre préférence 
                                            et de vos temps libres.
                                        </span>  
                                    </span>                          
                                </Col>
                                <Col md="4" className="service-element">
                                    <span className="service-element-index">06</span>  
                                    <span className="service-element-contain">
                                        <span className="service-element-title">Accueil, installation et accompagnement des étudiants</span>  
                                        <span className="service-element-description">
                                            Un programme d'accueil est mis en place pour chaque étudiant afin de l'aider dans ses démarches d'intégration. 
                                            Nous prenons grand soin de l'éduquer sur l'organisation, les astuces de vie, la culture étrangère, etc... dans le but 
                                            de l'aider à s'intégrer dans son nouvel environnement estudiantin.
                                        </span>  
                                    </span>                          
                                </Col>
                            </Row>
                        </Row> 
                    </section>  
                    <section className="w-100 testimony-section">
                        <h2>Ils nous ont fait confiance</h2>
                        <Row className="w-100 m-0">
                            <Col md="4" className="testimony-element">
                                <span className="testimony-element-photo"></span>  
                                <span className="testimony-element-contain">
                                    <span className="testimony-element-title">
                                        <span className="bold">NKONLACK AUDREY O. C.</span> <br />
                                        Ancien élève du lycée bilingue de Mendong à Yaoundé, étudiante à l'institut polytechnique des sciences avancées 
                                        de SFAX en Tunisie
                                    </span>  
                                    <span className="testimony-element-description">
                                        Avant de connaître CAMPUS UNIVERSEL, j'avais perdu un an à l'université par ce que je ne trouvais pas une très bonne
                                         école où je pouvais faire l'ingénierie pétrolière. <span className="highlight">Grâce à cette agence j'ai pu intégrer 
                                         l'Institut Polytechnique des sciences avancées de SFAX en Tunisie</span> où je fais ma filière de rêve. Ce qui me 
                                         séduit sur l'agence CAMPUS UNIVERSEL, c'est la rapidité avec laquelle elle gère les dossiers et le coût de la 
                                         procédure qui est au niveau du camerounais.
                                    </span>  
                                </span>                          
                            </Col>
                            <Col md="4" className="testimony-element">
                                <span className="testimony-element-photo"></span>  
                                <span className="testimony-element-contain">
                                    <span className="testimony-element-title">
                                        <span className="bold">WILLIAM KABALEU</span> <br />
                                        Ancien élève au collège le MAZENOD à Ngaoudéré, étudiant à l'institut polytechnique des sciences avancées 
                                        de SFAX en Tunisie
                                    </span>  
                                    <span className="testimony-element-description">
                                        CAMPUS UNIVERSEL est l'agence de voyage qui m'a permis de réaliser mon rêve de poursuivre mes études en 
                                        Tunisie. La disponibilité et la disposition de son staff à nous accompagner dans la réalisation de nos rêves 
                                        est ce qui a marqué les moments de montage de mon dossier de voyage.<span className="highlight"> Elle est fiable, rapide et moins coûteuse.</span>
                                    </span>  
                                </span>                          
                            </Col>
                            <Col md="4" className="testimony-element">
                                <span className="testimony-element-photo"></span>  
                                <span className="testimony-element-contain">
                                    <span className="testimony-element-title">
                                        <span className="bold">YAN BELINGA</span> <br />
                                        Ancien élève du collège JACQUES DE BERNON à Maroua, étudiant à l'Institut européen d'électronique et d'informatique 
                                        (EURELEC), campus Casablanca au Maroc
                                    </span>  
                                    <span className="testimony-element-description">
                                        J'ai connu CAMPUS UNIVERSEL lors de leur passage dans mon collège à Maroua; les responsables qui étaient passés étaient très 
                                        jeunes au point où je me demandais s'ils étaient capables de me faire intégrer mon école de rêve au Maroc, car  
                                        <span className="highlight"> plusieurs de mes camarades hésitaient à cause de leur jeunesse.</span> <br />
                                        <span className="highlight">Heureusement pour moi, mes parents ont accepté de saisir l'opportunité</span> et en quelques temps, mon
                                         dossier a été géré efficacement à moindre coût et en ce moment, je poursuis mes études en gestion des ressources humaines au Maroc.
                                    </span>  
                                </span>                          
                            </Col>
                        </Row> 
                        <section className="w-100 partner-section">
                            <Carousel
                                swipeable={false}
                                draggable={false}
                                showDots={false}
                                arrows={false}
                                responsive={carouselResponsive}
                                infinite={true}
                                autoPlay={true}
                                autoPlaySpeed={2000}
                                keyBoardControl={true}
                                customTransition="all .5"
                                transitionDuration={500}
                                containerClass="partner-carousel-container"
                                itemClass="partner-carousel-item" >
                                    <img src={partner1} alt="Partenaire" height="90px" />
                                    <img src={partner2} alt="Partenaire" height="90px" />
                                    <img src={partner3} alt="Partenaire" height="90px" />
                                    <img src={partner4} alt="Partenaire" height="90px" />
                                    <img src={partner5} alt="Partenaire" height="90px" />
                                    <img src={partner6} alt="Partenaire" height="90px" />
                                    <img src={partner7} alt="Partenaire" height="90px" />
                                    <img src={partner8} alt="Partenaire" height="90px" />
                                    <img src={partner9} alt="Partenaire" height="90px" />
                                    <img src={partner10} alt="Partenaire" height="90px" />
                                    <img src={partner11} alt="Partenaire" height="90px" />
                                    <img src={partner12} alt="Partenaire" height="90px" />
                                    <img src={partner13} alt="Partenaire" height="90px" />
                                    <img src={partner14} alt="Partenaire" height="90px" />
                                    <img src={partner15} alt="Partenaire" height="90px" />
                                    <img src={partner16} alt="Partenaire" height="90px" />
                                    <img src={partner17} alt="Partenaire" height="90px" />
                                    <img src={partner18} alt="Partenaire" height="90px" />                            
                            </Carousel>
                        </section>
                    </section> 
                    
                    <Footer />             
                </Row>
            );
        }
    }

}

export default Home;