 import React, { Component } from 'react';
 import Row from 'react-bootstrap/Row';
 import Col from 'react-bootstrap/Col';
 import {isMobile} from 'react-device-detect';
 import {Helmet} from "react-helmet";
 import Carousel from 'react-multi-carousel';
 import 'react-multi-carousel/lib/styles.css';
 
 import { Footer, Header } from "../components";
 import partner1 from "../images/partner1.jpg";
import partner2 from "../images/partner2.jpg";
import partner3 from "../images/partner3.png";
import partner4 from "../images/partner4.png";
import partner5 from "../images/partner5.png";
import partner6 from "../images/partner6.png";
import partner7 from "../images/partner7.jpg";
import partner8 from "../images/partner8.png";
import partner9 from "../images/partner9.jpg";
import partner10 from "../images/partner10.jpg";
import partner11 from "../images/partner11.jpg";
import partner12 from "../images/partner12.jpg";
import partner13 from "../images/partner13.jpg";
import partner14 from "../images/partner14.jpg";
import partner15 from "../images/partner15.png";
import partner16 from "../images/partner16.jpg";
import partner17 from "../images/partner17.png";
import partner18 from "../images/partner18.jpg";

import ingenierieImage from "../images/ingenierie.jpg";
import architectureImage from "../images/architecture.jpg";
import aeronautiqueImage from "../images/aeronautique.png";
import socialeImage from "../images/sociale.jpg";
import santeImage from "../images/sante.jpg";
import medecineImage from "../images/medecine.jpg";

const INGENIERIE="ingenierie", ARCHITECTURE="architecture",
      AERONAUTIQUE="aeronautique", SOCIALE="sociale",
      SANTE="sante", MEDECINE="medecine";

const carouselResponsive = {
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 5
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 4
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
      slidesToSlide: 1
    }
  };

 class Ecolefiliere extends Component {
     state = {
         ingenierieFiliere: ["GENIE PETROLIER", "GENIE ENERGETIQUE", "GEOMATIQUE ET TOPOGRAPHIE", 
         "INFORMATIQUE, TELECOMMUNICATIONS ET RESEAUX", "MASTERE EN ENERGIE RENOUVELABLE", 
         "GENIE ELECTROMECANIQUE", "MECANIQUE AUTO", 
         "INFORMATIQUE DE GESTION ET MAINTENANCE INFORMATIQUE", 
         "ÉLECTRONIQUE ET MAINTENANCE AUDIO-VIDEO", "GENIE CIVIL", 
         "GENIE DES SCIENCES ET TECHNOLOGIES DES ALIMENTS", 
         "SCIENCES ET TECHNOLOGIES AGROALIMENTAIRES", 
         "QUALITE ENVIRONNEMENTALE (QHSE)", "ANALYSES AGROALIMENTAIRES", 
         "DIETHETIQUE ET NUTRITION", "TECHNICO-COMMERCIAL", "MANAGEMENT ENVIRONNEMENTAL", 
         "MARKETING AGROALIMENTAIRE"],
         architectureFiliere: ["ARCHITECTURE"],
         aeronautiqueFiliere: ["INGENIERIE AERONAUTIQUE", "PILOTE DE LIGNE", "HOTESSE DE L’AIR", 
         "GEOMATIQUE TOPOGRAPHIQUE"],
         socialeFiliere: ["ADMINISTRATION DES AFFAIRES", "GESTION LOGISTIQUE ET TRANSPORT", 
         "GESTION DES RESSOURCES HUMAINES", "MANAGEMENT ET GESTION DES PME", "SCIENCES POLITIQUES", 
         "COMMUNICATION DES ENTREPRISES", "COMMERCE ET TECHNIQUES DE VENTE", "SECRETARIAT DE DIRECTION", 
         "COMMERCE INTERNATIONAL", "COMPTABILITE ET GESTION D'ENTREPRISE", "COMPTABILITE SUR ORDINATEUR", 
         "RELATIONS PUBLIQUES"],
         santeFiliere: ["TECHNIQUE SPECIALISE EN INFIRMIER POLYVALENTE", "TECHNIQUE SPECIALISE EN KINESITHERAPIE", 
         "TECHNICIEN EN INFIMIER AUXILIAIRE", "QUALIFICATION EN AIDE SOIGNANT", "DELEGUES MEDICAUX", 
         "QUALIFICATION EN SAGE FEMME", "IMAGERIE MEDICALE"],
         medecineFiliere: ["MEDECINE GENERALE", "MEDECINE PHARMACEUTIQUE", "MEDECINE BUCCODENTAIRE"],
         currentFiliere: ["GENIE PETROLIER", "GENIE ENERGETIQUE", "GEOMATIQUE ET TOPOGRAPHIE", 
         "INFORMATIQUE, TELECOMMUNICATIONS ET RESEAUX", "MASTERE EN ENERGIE RENOUVELABLE", 
         "GENIE ELECTROMECANIQUE", "MECANIQUE AUTO", 
         "INFORMATIQUE DE GESTION ET MAINTENANCE INFORMATIQUE", 
         "ÉLECTRONIQUE ET MAINTENANCE AUDIO-VIDEO", "GENIE CIVIL", 
         "GENIE DES SCIENCES ET TECHNOLOGIES DES ALIMENTS", 
         "SCIENCES ET TECHNOLOGIES AGROALIMENTAIRES", 
         "QUALITE ENVIRONNEMENTALE (QHSE)", "ANALYSES AGROALIMENTAIRES", 
         "DIETHETIQUE ET NUTRITION", "TECHNICO-COMMERCIAL", "MANAGEMENT ENVIRONNEMENTAL", 
         "MARKETING AGROALIMENTAIRE"],
         currentFiliereTitle: INGENIERIE,
         currentFiliereImage: ingenierieImage,
     };

     setCurrentFiliere = (filiere) => {
         switch(filiere) {
             case INGENIERIE:
                this.setState({
                    currentFiliere: this.state.ingenierieFiliere,
                    currentFiliereTitle: filiere,
                    currentFiliereImage: ingenierieImage
                });
                break;
            case AERONAUTIQUE:
                this.setState({
                    currentFiliere: this.state.aeronautiqueFiliere,
                    currentFiliereTitle: filiere,
                    currentFiliereImage: aeronautiqueImage
                });
                break;
            case ARCHITECTURE:
                this.setState({
                    currentFiliere: this.state.architectureFiliere,
                    currentFiliereTitle: filiere,
                    currentFiliereImage: architectureImage
                });
                break;
            case SOCIALE:
                this.setState({
                    currentFiliere: this.state.socialeFiliere,
                    currentFiliereTitle: filiere,
                    currentFiliereImage: socialeImage
                });
                break;
            case SANTE:
                this.setState({
                    currentFiliere: this.state.santeFiliere,
                    currentFiliereTitle: filiere,
                    currentFiliereImage: santeImage
                });
                break;
            case MEDECINE:
                this.setState({
                    currentFiliere: this.state.medecineFiliere,
                    currentFiliereTitle: filiere,
                    currentFiliereImage: medecineImage
                });
                break;
            default:
                this.setState({
                    currentFiliere: this.state.ingenierieFiliere,
                    currentFiliereTitle: filiere,
                    currentFiliereImage: ingenierieImage
                });
         }
     }
 
     render(){
         
         if(isMobile) {
             return <p>Mobile detected</p>;
         }else{
             return (
                 <Row className="w-100 m-0">
                     <Helmet>
                         <title>CAMPUS UNIVERSEL - Du rêve à la réalité</title>
                         <meta 
                             name="description" 
                             content="Campus Universel est l’Agence de Promotion des études Supérieures à l’Etranger. Nous orientons les étudiants au Cameroun, 
                             les accompagnons pour les procédures de voyages et les accueillons puis les installons dans plus de 15 pays cibles dans le monde."
                         />
                     </Helmet>
                     <Header />
                     <section className="filiere-section">                         
                         <Row className="w-100 m-0">
                            <Col md="4" className="m-0 p-0 filiere-title">
                                <h1>Nos filières d'étude</h1>
                                <ul>
                                    <li className={this.state.currentFiliereTitle===INGENIERIE ? "mt-0 active":"mt-0"} 
                                        onClick={()=>this.setCurrentFiliere(INGENIERIE)}>
                                        Ingénierie
                                    </li>
                                    <li className={this.state.currentFiliereTitle===ARCHITECTURE ? "active":""}
                                        onClick={()=>this.setCurrentFiliere(ARCHITECTURE)}>
                                        Architecture
                                    </li>
                                    <li className={this.state.currentFiliereTitle===AERONAUTIQUE ? "active":""}
                                        onClick={()=>this.setCurrentFiliere(AERONAUTIQUE)}>
                                        Aéronautique
                                    </li>
                                    <li className={this.state.currentFiliereTitle===SOCIALE ? "active":""}
                                        onClick={()=>this.setCurrentFiliere(SOCIALE)}>
                                        Sciences sociales, Gestion et Commerce
                                    </li>
                                    <li className={this.state.currentFiliereTitle===SANTE ? "active":""}
                                        onClick={()=>this.setCurrentFiliere(SANTE)}>
                                        Sciences de la santé et paramédicales
                                    </li>
                                    <li className={this.state.currentFiliereTitle===MEDECINE ? "active":""}
                                        onClick={()=>this.setCurrentFiliere(MEDECINE)}>
                                        Médecine
                                    </li>
                                </ul>
                            </Col>
                            <Col md="8" className="m-0 p-0 filiere-description">
                                <span className="w-100 image-container">
                                    <img src={this.state.currentFiliereImage} alt="" width="100%" height="auto" />
                                </span>
                                <ul className="w-100">
                                    {
                                        this.state.currentFiliere.map((filiere, index)=><li key={index.toString()}>{filiere}</li>)
                                    }
                                </ul>                                
                            </Col>                            
                         </Row>                         
                     </section>
                     <section className="w-100 partner-section mt-0">
                        <Carousel
                            swipeable={false}
                            draggable={false}
                            showDots={false}
                            arrows={false}
                            responsive={carouselResponsive}
                            infinite={true}
                            autoPlay={true}
                            autoPlaySpeed={2000}
                            keyBoardControl={true}
                            customTransition="all .5"
                            transitionDuration={500}
                            containerClass="partner-carousel-container"
                            itemClass="partner-carousel-item" >
                                <img src={partner1} alt="Partenaire" height="90px" />
                                <img src={partner2} alt="Partenaire" height="90px" />
                                <img src={partner3} alt="Partenaire" height="90px" />
                                <img src={partner4} alt="Partenaire" height="90px" />
                                <img src={partner5} alt="Partenaire" height="90px" />
                                <img src={partner6} alt="Partenaire" height="90px" />
                                <img src={partner7} alt="Partenaire" height="90px" />
                                <img src={partner8} alt="Partenaire" height="90px" />
                                <img src={partner9} alt="Partenaire" height="90px" />
                                <img src={partner10} alt="Partenaire" height="90px" />
                                <img src={partner11} alt="Partenaire" height="90px" />
                                <img src={partner12} alt="Partenaire" height="90px" />
                                <img src={partner13} alt="Partenaire" height="90px" />
                                <img src={partner14} alt="Partenaire" height="90px" />
                                <img src={partner15} alt="Partenaire" height="90px" />
                                <img src={partner16} alt="Partenaire" height="90px" />
                                <img src={partner17} alt="Partenaire" height="90px" />
                                <img src={partner18} alt="Partenaire" height="90px" />                            
                        </Carousel>
                    </section>
                    <Footer />                 
                 </Row>
             );
         }
     }
 
 }
 
 export default Ecolefiliere;