 import React, { Component } from 'react';
 import { Link } from 'react-router-dom';
 import Row from 'react-bootstrap/Row';
 import Col from 'react-bootstrap/Col';
 import Button from 'react-bootstrap/Button';
 import {isMobile} from 'react-device-detect';
 

 import logosmallwhite from "../images/logo-small-white.png"; 

 class Footer extends Component {
 
     render(){
 
         if(isMobile) {
             return <p>Mobile detected</p>;
         }else{
             return (
                <section className="footer-section w-100 m-0">
                    <Row className="w-100 m-0">
                        <Col md="4" className="footer-section-one">
                            <img src={logosmallwhite} alt="Logo" className="footer-logo" />
                            <p><span className="bold">Siège social:</span> Polytechnique Yaoundé, Immeuble pharmacie EMIA, dernier niveau</p>
                            <p><span className="bold">Agence régionale du littoral:</span> PK15 Douala, Immeuble Express Union, dernier niveau</p>
                            <p><i class="fa fa-phone" aria-hidden="true"></i> +237 696 634 440 / 651 668 950</p>
                            <p><i class="fa fa-paper-plane" aria-hidden="true"></i> infosline@campusuniversel.org</p>
                        </Col>
                        <Col md="8" className="footer-section-two">
                            <nav className="w-100">
                                <Link to="/">Accueil</Link>
                                <Link to="/">Procédures</Link>
                                <Link to="/ecolefiliere">écoles et filières</Link>
                                <Link to="/apropos">à propos</Link>
                            </nav>
                            <Row className="w-100 m-0 footer-section-two-infos">
                                <a href="https://forms.gle/mbJirnQb1Ad1s35z7">
                                    <Button>Demandez plus d'informations</Button>
                                </a>
                                <span>
                                    <a href="https://web.facebook.com/CampusUniversel"><i class="fa fa-facebook"></i></a>
                                    <a href="https://www.instagram.com/campusuniversel/"><i class="fa fa-instagram"></i></a>
                                </span>                                    
                            </Row>
                            <Row className="w-100 m-0 footer-section-two-copyright">
                                <p>&copy; CAMPUS UNIVERSEL. Tous droits réservés.- Designed by <a href="https://www.amanitechnologies.com">AT</a></p>
                            </Row>
                        </Col>
                    </Row>
                </section>  
            );
         }
     }
 
 }
 
 export default Footer;